<template>
  <div class="validate">
    <cynav></cynav>
    <cymenu></cymenu>
    <div class="cy_example">
        <p class="cy_title">示例效果</p>
        <div class="line"></div>
        <div class="codeArea">

<codemirror ref="myEditor1" class="myEditor" :options="cmOptions"></codemirror>
<xmp ref="myEditorVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import validate from "cy_static/validate";
    import ui from "cy_static/ui";
    var valid = validate ({username:""}, {
      username : {
        min : 1,
        max : 10,
        onError : "收货人不能为空",
        onErrorMax : "收货人最多10个字符"
      }
    });
    if (valid.pageIsValid(function(error) {
      ui.toast(error);
      return true;
    }));
&lt;/script&gt;
</xmp >

        </div>
        <div class="submitCode">
            <input type="button" class="btn-submit" value="点击运行代码" @click="runcode()">
        </div>
        <div class="iframeBox">
            <iframe ref="iframe" src="#/runcode"></iframe>
        </div>
    </div>   
    <div class="cy_center">
      <h2 class="cy_h2">validate 标签页</h2>  
      <div class="codeModule">
          <h3 class="cy_h3">引入</h3> 
<codemirror ref="codeStyle1" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal1" style="display:none">
&lt;script type="text/javascript"&gt;
    import validate from "cy_static/validate";
&lt;/script&gt;
</xmp >
  
      </div>

      <h2 class="cy_h2">代码演示</h2>
      <div class="codeModule">
<codemirror ref="codeStyle2" class="codeStyle" :options="cmOptions1"></codemirror>
<xmp ref="codeStyleVal2" style="display:none">
&lt;script type="text/javascript"&gt;
  import validate from "cy_static/validate";
  import ui from "cy_static/ui";
  var valid = validate({username:"", mobileid:""}, {
      username : {
        min : 1,
        max : 10,
        onError : "收货人不能为空",
        onErrorMax : "收货人最多10个字符"
      },
      mobileid : {
        validtype : "regType",
        regExp : "^1\\d{10}$",
        onError : "手机号码格式错误"
      }
  });
  if (valid.pageIsValid(function(error) {
      ui.toast(error);
      return true;
  }));
&lt;/script&gt;
</xmp >

      </div>

      <h2 class="cy_h2">API</h2>
      <div class="apiModule">
        <h2 class="cy_h2">构造方法</h2>				
         <ul class="ul-props tableList">
          <li>
            <p><a>validate</a>(obj : Object, params : Object) : <a>Boolean</a></p>
						<p>设置校验项和校验规则。obj 校验对象，params 校验规则；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border" style="margin-top: 12px;">
              <tr>
                  <th colspan="5">params 校验规则之validtype属性值详解</th>
              </tr>
              <tr>
                  <th colspan="5">inputValid</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>type</td>
                <td class="atn">String</td>
                <td>"size"</td>
                <td>
                  (对select无效)<br>
                  "size":表示比较长度； <br>
                  "number":数值型比较；<br>
                  "string":字符型比较；<br>
                  "date":短日期类型；<br>
                  "datetime":长日期类型
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>min</td>
                <td class="atn">String</td>
                <td>0</td>
                <td>
                  最小长度/值	0<br>
                  输入框比较的是关系；<br>
                  单|多选校验选项个数
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>max</td>
                <td class="atn">String</td>
                <td>99999999999999</td>
                <td>
                  最大长度/值	<br>
                  输入框比较的是关系；<br>
                  单|多选校验选项个数
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onErrorMin</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>比min属性小的提示</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onErrorMax</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>比max属性大的提示</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>empty</td>
                <td class="atn">object</td>
                <td>
                  {<br>
                      leftEmpty: true,<br>
                      rightEmpty: true,<br>
                      emptyError: null<br>
                  };
                </td>
                <td>
                  leftEmpty:左边是否允许为空；<br>
                  rightEmpty:右边是否允许为空； <br>
                  emptyError：没有提示语时，显示onError提示语
                </td>
                <td class="text-c">N</td>
              </tr>
            </table>
            <table class="table-props border">            
              <tr>
                  <th colspan="5">regType</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>regExp</td>
                <td class="atn">String|Array</td>
                <td>""</td>
                <td>
                  正则表达式或regexEnum对象的属性名
                </td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>dataType</td>
                <td class="atn">String</td>
                <td>"string"</td>
                <td>
                  "string":自定义的表达式；<br>
                  "enum":regexEnum属性名
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>operateor</td>
                <td class="atn">String</td>
                <td>""</td>
                <td>
                  多个表达式时的运算符类型；<br>
                  "||"或的关系   "&amp;&amp;"并列
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
            </table>
            <table class="table-props border">
              <tr>
                  <th colspan="5">funType</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>fun</td>
                <td class="atn">Function</td>
                <td>function(){}</td>
                <td>
                  外部函数名<br>
                  参数1:元素的值；<br>
                  参数2:元素对象；<br>
                  返回string为自定义错误
                </td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>
                  函数返回false错误的提示;
                </td>
                <td class="text-c">N</td>
              </tr>
            </table>
            
            <table class="table-props border">
              <tr>
                  <th colspan="5">compareType</th>
              </tr>
              <tr>
                <th style="width:20%">参数名</th>
                <th style="width:20%">类型</th>
                <th style="width:20%">默认值</th>
                <th style="width:30%">描述</th>
                <th style="width:10%" class="text-c">必需</th>
              </tr>
              <tr>
                <td>desID</td>
                <td class="atn">string|object</td>
                <td>""</td>
                <td>要跟源目标进行比较的目标组件（选择器|对象）</td>
                <td class="text-c">Y</td>
              </tr>
              <tr>
                <td>operateor</td>
                <td class="atn">string</td>
                <td>""</td>
                <td>
                  比较符号：=、!=、&gt;、&gt;=、&lt;、&lt;=
                </td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>dataType</td>
                <td class="atn">string</td>
                <td>"string"</td>
                <td>值："string"、"number","datetime","date"</td>
                <td class="text-c">N</td>
              </tr>
              <tr>
                <td>onError</td>
                <td class="atn">String</td>
                <td>"输入错误"</td>
                <td>错误时的提示语</td>
                <td class="text-c">N</td>
              </tr>
            </table>
          </li>
				</ul>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">实例方法</h2>
        <ul class="ul-props">
					<li>
						<p><a>pageIsValid</a>(callback : Function) : <a>Boolean</a></p>
						<p>检查各项是否验证通过，返回校验结果；(参数callback每一项校验不通过时都会调用该函数；当该函数返回true时不再校验后面各项验证；)</p>
					</li>
					<li>
						<p><a>oneIsValid</a>(attr : String) : <a>object</a></p>
						<p>检查单项是否验证通过；(参数attr为要校验的数据属性值；返回值object；里面包含isValid属性表示校验是否通过;)</p>
					</li>
				</ul>
      </div>

      <div class="apiModule">
        <h2 class="cy_h2">静态方法</h2>
        <ul class="ul-props">
          <li>
						<p><a>addRule</a>(validtype : String, func : Function) : <a>Void</a></p>
						<p>添加校验规则；validtype校验类型，func校验函数；</p>
            <div class="showDetail">详细</div>
            <table class="table-props border">
              <tr>
                <td>
<codemirror ref="demoCode1" class="demoCode" :options="cmOptions2"></codemirror>
<xmp ref="demoCodeVal1" style="display:none">
&lt;script type="text/javascript"&gt;
  validate.addRule("isMobile",function(value, params){
      var setting = Object.assign({
          onError : "手机号码格式错误"
      }, params || {});
      var returnObj = {isValid:true};
      if (!/^1\d{10}$/.test(value)) {
          returnObj.isValid = false;
          returnObj.onError = setting.onError;
      }
      return returnObj
  });
  // 使用
  var valid = validate({mobileid : 15604656355}, {
      mobileid : {
          validtype : "isMobile",
          onError : "手机号码格式错误"
      }
  });

&lt;/script&gt;
</xmp >
                </td>
              </tr>
            </table>
					</li>
					<li>
						<p><a>serialize</a>(form : Object|String = null) : <a>String</a></p>
						<p>输出序列化表单值的结果；</p>
					</li>
					<li>
						<p><a>serializeArray</a>(form : Object|String = null) : <a>Array</a></p>
						<p>输出以数组形式序列化表单值的结果；</p>
					</li>
				</ul>
      </div>



    </div> 
  </div>
</template>

<script>
import {showDetail,trim} from "../../../assets/js/codeStyle";
export default {
  name: 'validate',
  data () {
    return {
      msg: 'validate',
      cmOptions:{
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true

      },
      cmOptions1:{
            readOnly:"true",
            theme : "darcula",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      },
      cmOptions2:{
            readOnly:"true",
            theme : "mdn-like",
            mode : "text/html",  // 模式
            indentUnit : 2,  // 缩进单位，默认2
            smartIndent : true,  // 是否智能缩进
            tabSize : 4,  // Tab缩进，默认4
            showCursorWhenSelecting : true
      }
    }
  },
  mounted(){
    var m = this;
    ["myEditor", "codeStyle", "demoCode"].forEach((element)=>{
        var dom, valDom, index = 0, getDom = function() {
            index++;
            dom = m.$refs[element + index];
            valDom = m.$refs[element + "Val" + index];
        };
        getDom();
        while (dom) {
            dom.codemirror.setValue(trim(valDom.innerHTML));
            getDom();
        }
    });
    showDetail();
  }, 
  methods:{
    runcode(){
      var m = this, frame = m.$refs.iframe;
      frame.src = "?a=" + Math.random() + "#/runcode"
      frame.onload = function() {
          frame.contentWindow.postMessage(m.$refs.myEditor1.codemirror.getValue(), "*");
      }

    }
  }
}
</script>  
<style scoped>
  .cy_example .codeArea{height:320px}
  .cy_example .iframeBox{flex:10}
  .tableList table{margin-top: 0;border-bottom:3px solid #f7f7f7;} 
</style>



